@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.message-not-found-page {
    width: 100%;
    height: 100%;
    display: inline-flex;
    justify-content: center;
}

.message-div {
    display: inline-flex;
    color: white;
    font-weight: 100;
    width: 50%;
    min-width: 300px;
    margin-top: 17.5vh;
    height: auto;
    background-color: white;
    border-radius: 1vh;
    border: 1px solid black;
    padding: 1vh;
}

.message-div-body {
    width: 66%;
    height: auto;
}

.message-div-logo {
    width: 33%;
    min-width: 100px;
    margin: auto;
}

.message-div-logo img {
    width: 100%;
}

.top-section {
    display: inline-flex;
    padding: 1vh;
}

.bottom-section {
    border-top: 1px solid #0984e3;
    padding-left: 1vh;
    display: inline-block;
}

.error-message {
    color: black;
    font-size: 4vh;
}

.message-div .error {
    color: #0984e3;
    font-size: 2vh;
    margin: auto;
    margin-left: 1vw;
}

.message-div p, a {
    font-size: 2vh;
    color: black;
}

.message-div a {
    font-size: 2vh;
    color: #0984e3;
}


@media (max-width: 800px) 
{
    .top-section {
        display: inline-block;
    }

}
